.js-jump-to-nav-smooth-scroll {
  scroll-behavior: smooth !important;
}

:root {
  --jtn-idle-width: 40px;
  --jtn-base-font-size: 16px;
  --jtn-ff-primary: "Helvetica Neue", Helvetica, Arial, sans-serif;
  --jtn-common-space: .8125em;
  --jtn-border-radius: .75em;
  --jtn-bg-color: #fff;
  --jtn-text-color: #333;
  --jtn-link-color: #1f4de3;
  --jtn-link-color-dark: #1435a1;
  --jtn-gray-50: #f3f3f3;
  --jtn-gray-100: #e6e6e6;
  --jtn-gray-200: #cdcdcd;
  --jtn-color-border-common: rgba(0, 0, 0, 0.05);
  --jtn-color-highlight: #d4ddfa;
  --jtn-ease: cubic-bezier(0.19, 1, 0.22, 1);
  --jtn-transition-speed: 400ms;
  --jtn-box-shadow-deep: 3px 5px 25px rgba(0, 0, 0, .18);
}

.jump-to-nav-wrapper {
  position: fixed;
  top: 6.25em;
  right: 1.125em;
  font-size: var(--jtn-base-font-size) !important;
  filter: drop-shadow(3px 5px 15px rgba(0, 0, 0, 0.18));
  pointer-events: none;
}
.jump-to-nav-wrapper *,
.jump-to-nav-wrapper :after,
.jump-to-nav-wrapper :before {
  box-sizing: border-box;
}
.jump-to-nav-wrapper .jump-to-nav {
  overflow: hidden;
  width: 15em;
  height: auto;
  transition: clip-path var(--jtn-transition-speed) var(--jtn-ease);
  color: var(--jtn-text-color);
  border-radius: var(--jtn-border-radius);
  background-color: var(--jtn-bg-color);
  font-family: var(--jtn-ff-primary);
  clip-path: inset(0 0 calc(100% - var(--jtn-idle-width)) calc(100% - var(--jtn-idle-width)) round var(--jtn-border-radius));
}
.jump-to-nav-wrapper .jump-to-nav > *:not(.jump-to-nav__maximize) {
  visibility: hidden;
  transition: visibility var(--jtn-transition-speed) var(--jtn-ease);
  transition-delay: var(--jtn-transition-speed);
  pointer-events: none;
}
.jump-to-nav-wrapper .jump-to-nav > *:not(.jump-to-nav__maximize) a:focus,
.jump-to-nav-wrapper .jump-to-nav > *:not(.jump-to-nav__maximize) button:focus {
  outline: 0;
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__maximize {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--jtn-idle-width);
  height: var(--jtn-idle-width);
  cursor: pointer;
  border: 0;
  background-color: var(--jtn-bg-color);
  pointer-events: auto;
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__maximize svg {
  position: relative;
  right: 1px;
  width: 0.5625em;
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__minimize {
  width: 28px;
  height: 28px;
  margin-left: auto;
  padding: 0;
  cursor: pointer;
  border: 0;
  border-radius: 30px;
  background-color: transparent;
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__minimize:hover {
  background-color: var(--jtn-gray-100);
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__link {
  display: inline-block;
  margin-bottom: 0.5em;
  color: var(--jtn-link-color);
  font-size: 0.75em;
  font-weight: normal;
  text-decoration: none;
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__list,
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__nested-list {
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
  line-height: 1.2;
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__list {
  margin-top: 0;
  margin-left: -0.625em;
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__item {
  padding-left: 0.625em;
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__header {
  display: flex;
  align-items: center;
  min-height: 3.125em;
  flex-wrap: wrap;
  padding: 0.625em 1.125em 0 1.125em;
  border-bottom: 1px solid var(--jtn-color-border-common);
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__body {
  padding: 0.625em 1.125em;
  border-right: 1px solid #fff;
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__body::-webkit-scrollbar-corner {
  background-color: transparent;
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__body::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__body::-webkit-scrollbar-track {
  background-color: transparent;
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__body::-webkit-scrollbar-thumb {
  outline: 0;
  background-color: var(--jtn-gray-100);
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__body::-webkit-scrollbar-thumb:hover {
  background-color: var(--jtn-gray-200);
}
.jump-to-nav-wrapper .jump-to-nav [role=heading] {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: antialiased;
  font-weight: bold;
}
.jump-to-nav-wrapper .jump-to-nav [role=heading][aria-level="1"] {
  font-size: 1.0625em;
}
.jump-to-nav-wrapper .jump-to-nav [role=heading][aria-level="2"] {
  margin-bottom: 0.5em;
  font-size: 0.875em;
}
.jump-to-nav-wrapper .jump-to-nav .jump-to-nav__item--active > .jump-to-nav__link {
  font-weight: bold;
}
.jump-to-nav-wrapper.jump-to-nav-wrapper--showing {
  pointer-events: auto;
}
.jump-to-nav-wrapper.jump-to-nav-wrapper--showing .jump-to-nav {
  clip-path: inset(0 0 0 0 round var(--jtn-border-radius));
}
.jump-to-nav-wrapper.jump-to-nav-wrapper--showing .jump-to-nav > * {
  visibility: visible;
  transition-delay: 0ms;
  pointer-events: auto;
}
.jump-to-nav-wrapper.jump-to-nav-wrapper--showing .jump-to-nav > * a:focus,
.jump-to-nav-wrapper.jump-to-nav-wrapper--showing .jump-to-nav > * button:focus {
  outline: auto;
}
.jump-to-nav-wrapper.jump-to-nav-wrapper--showing .jump-to-nav .jump-to-nav__maximize {
  display: none;
}
.jump-to-nav-wrapper.jump-to-nav-wrapper--showing .jump-to-nav .jump-to-nav__body {
  overflow: auto;
}
.jump-to-nav-wrapper .jump-to-nav__search {
  width: 100%;
  flex: 0 0 100%;
  font-size: 0.75em;
  position: relative;
  margin: var(--jtn-common-space) 0;
}
.jump-to-nav-wrapper .jump-to-nav__search-icon {
  width: 12px;
  height: 12px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 13px;
}
.jump-to-nav-wrapper .jump-to-nav__search-icon path {
  fill: var(--jtn-text-color);
}
.jump-to-nav-wrapper .jump-to-nav__search-input {
  width: 100%;
  border: 0;
  border-radius: 4px;
  background-color: var(--jtn-gray-50);
  height: 32px;
  padding-left: 32px;
  padding-right: 20px;
  margin: 0;
}
.jump-to-nav-wrapper .jump-to-nav__search-input:focus {
  outline-color: var(--jtn-link-color);
}
.jump-to-nav-wrapper .autoComplete_wrapper {
  width: 100%;
  flex: 0 0 100%;
  position: relative;
}
.jump-to-nav-wrapper .autoComplete_wrapper > ul {
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  background-color: var(--jtn-bg-color);
  list-style-type: none;
  width: 100%;
  padding: 0;
  margin: 0;
  border-radius: 4px;
  box-shadow: var(--jtn-box-shadow-deep);
  overflow: auto;
  max-height: 160px;
}
.jump-to-nav-wrapper .autoComplete_wrapper li {
  padding: 8px 15px;
  border-bottom: 1px solid var(--jtn-gray-100);
  font-weight: bold;
}
.jump-to-nav-wrapper .autoComplete_wrapper li:last-child {
  border-bottom: 0;
}
.jump-to-nav-wrapper .autoComplete_wrapper mark {
  background-color: var(--jtn-gray-100);
}
.jump-to-nav-wrapper .autoComplete_wrapper [aria-selected=true] {
  background-color: var(--jtn-link-color);
  color: #fff;
}
.jump-to-nav-wrapper .autoComplete_wrapper [aria-selected=true] mark {
  background-color: var(--jtn-link-color-dark);
  color: #fff;
}